@use "../../utils" as *;
/*----------------------------------------*/
/*  4.25 Neighbour area css
/*----------------------------------------*/

.bd-neighbour-area {
    background-repeat: no-repeat;
    background-size: revert;
    background-position: top left;
}
.neighbour-area-item {
	position: relative;
	z-index: 5;
	&:before {
		position: absolute;
		content: "";
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
		background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #060615 100%);
	}
    .content {
        position: absolute;
        bottom: 0;
        padding: 25px 25px 25px;
        width: 100%;
        z-index: 10;
        .info {
            color: rgba(255,255,255,.6);
            margin-bottom: 5px;
            display: block;
        }
        .title {
            color: var(--bd-white);
            font-size: 24px;
            @media #{$lg,$md} {
                font-size: 22px;
            }
            @media #{$sm,$xs} {
                font-size: 20px;
            }
            a{
                    &:hover {
                        color: var(--bd-primary);
                    }
            }
        }
    }
    .thumb {
        img {
            width: 100%;
            min-height: 300px;
        }
    }
    .link {
        width: 40px;
        height: 40px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        color: var(--bd-black);
        background: #ffff;
        position: absolute;
        top: 30px;
        right: 20px;
        font-size: 18px;
        opacity: 0;
        visibility: hidden;
        &:hover {
            background-color: var(--bd-primary);
            color: var(--bd-white);
        }
    }
    &:hover {
        .link {
            top: 20px;
            opacity: 1;
            visibility: visible;
        }
    }
}
